mark {
  background: white;
  padding: 0.3rem 0.7rem;
  border-radius: 3px;
  color: #5DBFD6;
  border: 1px solid #5DBFD6;
  cursor: pointer;
  margin: 0 0.25rem;
  font-size: 100%;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
mark br {
  display: none;
}
mark::after {
  content: "Óûù";
  font-family: element-icons !important;
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: baseline;
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #5DBFD6;
  font-size: 14px;
  vertical-align: text-top;
  margin-left: 10px;
}

.phrase .el-button {
  padding: 10px;
  font-size: 12px;
}

div[contenteditable=true] {
  min-height: 140px;
  line-height: 2.5;
  overflow: hidden;
  resize: none;
  font-size: 12px;
}
div[contenteditable=true].oneline {
  min-height: inherit;
  white-space: nowrap;
}

#textarea-box {
  position: relative;
}
#textarea-box .phrase {
  margin: 15px 0;
}
#textarea-box :global p {
  margin: 0;
}

.tox-notifications-container {
  display: none !important;
}